  font-family: Tangerine;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format(truetype);
}

@font-face {
  font-family: "Montserrat Alternates";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
}

html {
  box-sizing: inherit;
  font-size: 62.5%;
  line-height: 1.6;
}

body {
  font-family: "Montserrat Alternates", serif;
  background-color: #ccc8c8;
  font-size: 1.6rem;
}

img {
  display: block;
  width: 100%;
}

.icon-container {
    width: 40px;
    height: 40px;
    display: inline-block;
}

.icon-container img {
    display: inline-block;
}

#content {
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

header {
  display: flex;
  background-color: white;
  color: black;
  width: 100%;
  font-size: 2.4rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

header h1 {
  width: 100%;
  font-family: Tangerine, cursive;
  text-align: center;
  padding: 12px;
  margin: 6px 0;
  line-height: 1.2;
  font-size: 8rem;
}

header nav {
  display: flex;
  width: 100%;
  justify-content: center;
  gap: 20px;
  padding: 8px;
  height: 100px;
}

header nav button {
  outline: none;
  border: none;
  padding: 18px 20px;
  font-family: inherit;
  background-color: transparent;
  transition: all ease-in-out 0.2s;
  margin-bottom: 4px;
  cursor: pointer;
  color: black;
}

header nav button.active {
  background-color: #ccc8c8;
}

header nav button:hover {
  margin-bottom: 18px;
  padding-top: 0px;
}

/* Main Content */
main {
  background-color: #eee;
  max-width: 700px;
  padding: 4rem;
  margin: 6rem 8px;
  font-size: 2rem;
}

.home-tab-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 530px;
  width: 630px;
}

.home-tab-content img {
  width: 50%;
  margin-top: 3rem;
  margin-bottom: 3rem;
  opacity: 0.9;
}

.menu-tab-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
}

.menu-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 300px;
  padding: 4rem;
  cursor: pointer;
}

.menu-item:hover {
    background-color: rgba(204, 200, 200, 0.8);
}

.menu-ingredients {
  font-size: 1.5rem;
  text-align: center;
}

.contact-tab-content p {
    display: flex;
    align-items: center;
    padding: 1rem;
    gap: 1rem;
}

footer {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  width: 100%;
}

footer a {
  text-decoration: none;
  color: black;
}

footer .icon-container {
  display: inline-block;
  width: 40px;
  height: 40px;
}

@media (max-width: 700px) {
  .home-tab-content,
  .contact {
    height: 100%;
    width: 100%;
  }
  .menu-tab-content {
    grid-template-columns: 1fr;

  }
  .menu img {
    width: 50%;
  }
}

@media (max-width: 500px) {
  html {
    font-size: 57.5%;
  }
  .home-tab-content img {
    width: 80%;
  }
}
